.form {
  position: relative;
  max-width: 400px;
  margin: 30px auto 0;
  background: white;
  border-radius: 30px;
  box-sizing: border-box;
}

.label {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-size: 18px;
  color: #707981;
  margin-left: 10px;
}

.inputForm {
  display: block;
  width: 100%;
  padding: 0 20px;
  margin-top: 10px;
  margin-bottom: 20px;
  background: #e9eff6;
  line-height: 40px;
  border-width: 0;
  border-radius: 4px;
  font-family: 'Roboto', sans-serif;
  box-sizing: border-box;
}

.btnForm {
  width: 100%;
  margin-top: 30px;
  padding: 0 20px;
  margin-bottom: 10px;
  line-height: 40px;
  border-width: 0;
  border-radius: 4px;
  font-family: 'Roboto', sans-serif;
  background: #8c7c75;

  color: white;
  font-size: 14px;
  cursor: pointer;
}

.btnForm:hover,
.btnForm:focus {
  background: #2c2624;
}
